/* Parent container holding editors and button */
.page-container {
    justify-content: space-evenly;
    align-items: stretch;
    display: flex;
    flex-direction: column;
    height: 80vh;
    margin: 0;
    padding: 0;
    gap: 20px; /* Keeps spacing between editors and button */
}

.editors-container {
    display: flex;
    justify-content: center; 
    align-items: stretch;
    flex: 1;
    padding: 0 20px; 
    gap: 50px; 
    min-height: 0; 
}

.left-box, .right-box {
    flex: 1 1 0%; 
    box-sizing: border-box;
    min-width: 400px; 
    max-width: calc(50% - 25px); /* Ensure stability with gap taken into account */
}

.editor-container {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    border-radius: 15px;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.3);
}


/* Button container */
.center-button {
    display: flex; 
    justify-content: center; 
    align-items: center; 
    margin-top: 0px; 
}
